@import "ScssHelpers/_helpers_all.scss";

.efficient_work {
	position: relative;
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding-bottom: 14.6rem;
	}

	@include bp($point_3, $point_4) {
		padding-bottom: 9.4rem;
	}

	@include bp($point_3) {
		padding: 6.4rem var(--gl-indent);

		background-color: var(--primary_color);
	}

	&::before {
		z-index: 100;
		position: absolute;
		left: 0;
		top: 0;

		height: 100%;


		@include bp($point_2, min) {
			width: calc((100vw - $page_width) / 2);
			background-color: inherit;
		}
		@include bp($point_3, $point_4) {
			width: var(--gl-indent);
			background-color: var(--primary_color);
		}

		@include bp($point_4, min) {
			content: '';
		}
	}
}

.efficient_work_head {
	@extend %slider_head;

	@include bp($point_3) {
		display: flex;
		flex-wrap: wrap;
	}
	@include bp($point_3, $point_4) {
		margin-bottom: 4rem;
	}
	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
	}
}

.efficient_work_title {
	@extend %slider_title;

	@include bp($point_3) {
		color: var(--white_color);
	}

	@include bp($point_4 - 1) {
		font-size: 2.5rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.efficient_work_descr {
	width: 100%;

	line-height: 1.56;
	color: var(--white_color);

	@include bp($point_2, min) {
		display: none;
	}
	@include bp($point_3, $point_4) {
		font-size: 2.1rem;
	}
	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}

.efficient_work_content {
	@extend %global_flex_block_row_nowrap_flex-start_stretch;

	@include bp($point_4 - 1) {
		margin-bottom: 3.4rem;
	}
}

.efficient_work_static {
	z-index: 100;
	position: relative;
	flex-shrink: 0;
	width: 44.8rem;
	min-height: 44.8rem;
	padding: 17.8rem 7rem 4rem;
	background-color: var(--red_color);

	@include bp($point_3) {
		display: none;
	}
}

.efficient_work_static__icon {
	position: absolute;
	right: .4rem;
	top: 0;

	width: 20rem;
	height: 20rem;
}

.efficient_work_static__title {
	@extend %h6;

	color: var(--white_color);
	letter-spacing: -.03em;

	&:not(:last-child) {
		margin-bottom: 3.5rem;
	}
}

.efficient_work_static__descr {
	font-size: 2rem;
	font-weight: 700;
	color: var(--white_color);
}

.efficient_work_slider {
	margin: 0;

	@include bp($point_2, min) {
		width: calc(100% - 48.8rem);
	}

	@include bp($point_3) {
		width: 100%;
	}

	@include bp($point_4, min) {
		overflow: visible;
	}
}

.efficient_work_slide {
	position: relative;

	border-top: 1px solid var(--border_grey_color);
	border-bottom: 1px solid var(--border_grey_color);
	background-color: var(--white_color);


	@include bp($point_2, min) {
		width: 44.8rem;
	}

	@include bp($point_4, min) {
		min-height: 44.8rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 100%;
		width: 100%;
	}

	&:not(:last-child) {
		border-right: 1px solid var(--border_grey_color);
	}

	&:first-child {
		@include bp($point_3) {
			border-left: 1px solid var(--border_grey_color);
		}
	}
}

.efficient_work_slide__icon {
	position: absolute;
	right: 0;
	top: 0;

	background-color: #eef3f9;

	@include bp($point_4, min) {
		padding: 3.6rem;
		width: 12.4rem;
		height: 12.4rem;
	}
	@include bp($point_4 - 1) {
		padding: 1.8rem;
		width: 6.2rem;
		height: 6.2rem;
	}
}

.efficient_work_slide__content {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	@include bp($point_4, min) {
		padding: 19.4rem 7rem 2rem;
	}

	@include bp($point_4 - 1) {
		padding: 9.6rem 3rem 4rem;
	}
}

.efficient_work_slide__info {
	@include flex_block(row, nowrap, flex-start, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.5rem;
		}
	}
}

.efficient_work_slide__item {
	color: var(--gray_color);
	font-family: $biotif_font;
	letter-spacing: -0.02rem;
	line-height: 1.3;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

a.efficient_work_slide__item {
	@extend %transition_color;
	&:hover,
	&:focus {
		text-decoration: none;
		color: var(--primary_light_color);
	}

	&::after {
		content: "";
		display: block;
		width: 0.4rem;
		height: 0.4rem;

		background-color: var(--gray_color);
	}

	@include flex_block(row, nowrap, flex-start, center, center);

	@include bp($point_2, min) {
		&::after {
			margin: 0 1.6rem 0 1.3rem;
		}
	}

	@include bp($point_3, $point_4) {
		&::after {
			margin: 0 1.4rem 0 1.3rem;
		}
	}

	@include bp($point_4 - 1) {
		&::after {
			margin: 0 1rem 0 1.1rem;
		}
	}
}

.efficient_work_slide__link {
	display: block;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_2, min) {
		max-width: 27.6rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 29rem;
	}

	@include bp($point_4 - 1) {
		max-width: 24rem;
	}
}

.efficient_work_slide__title {
	display: block;

	color: var(--primary_color);
	font-family: $biotif_font;
	font-weight: 600;
	line-height: 1.32;
	letter-spacing: -0.03em;

	@extend %transition_color;

	@include bp($point_2, min) {
		font-size: 2.4rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 2.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}
}

.efficient_work_slide__title:hover,
.efficient_work_slide__title:focus {
	text-decoration: none;

	color: var(--primary_light_color);
}

// Progress bar
.efficient_work_bottom {

	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.efficient_work_bottom__value {
	font-family: $biotif_font;
	font-size: 1.3rem;
	color: var(--white_color);
}

.efficient_work_bottom__progress {
	@extend %slider_nav_progress;

	background-color: #4c619a;

	.swiper-pagination-progressbar-fill {
		background-color: var(--white_color);
	}
}

// arrows
.efficient_work_navigation {
	@extend %slider_nav;

	@include bp($point_3) {
		display: none;
	}
}

.efficient_work_arrow {
	@extend %slider_nav_arrow;
}

.efficient_work_icon {
	@extend %slider_nav_icon;

	.efficient_work_arrow--prev & {
		transform: rotate(-180deg);
		margin-left: -0.3rem;
	}

	.efficient_work_arrow--next & {
		margin-right: -0.3rem;
	}
}
