@import "ScssHelpers/_helpers_all.scss";

.blog_section {
	background-color: var(--light_color);
	padding-bottom: 0;

	@include bp($point_2, min) {
		padding-top: 15.4rem;
		padding-bottom: 7.8rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 7.3rem;
		padding-bottom: 4.4rem
	}

	@include bp($point_4 - 1) {
		padding-top: 6.8rem;
		padding-bottom: 2rem;
	}
}

.hero_article_section {

	padding-bottom: 0;
	padding-top: 0;
	
}

.blog_section__title_wp {
	@extend %global_flex_block_row_wrap_flex-start_flex-end;

	@include bp($point_2, min) {
		margin-bottom: 14.3rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 6.3rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 3.9rem;
	}
}

.blog_section__title {
	@extend %h1;

	margin-right: auto;

	color: var(--primary_dark_color);

	@include bp($point_4 - 1) {
		display: flex;
		align-items: center;

		width: 100%;

		margin-bottom: 3.5rem;
	}
}

.blog_section__title_dot {
	color: var(--dot-color);
	font-family: "Arial";

	@include bp($point_4 - 1) {
		margin-right: auto;
	}
}

.blog_section__decor_dots {
	@extend %global_flex_block_row_wrap_flex-start_center;

	@include bp($point_2, min) {
		margin-bottom: 4.3rem;
		margin-left: 1rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 2.8rem;
		margin-left: 0.5rem;
	}

	@include bp($point_4 - 1) {
		order: 3;

		padding-top: 0.6rem;
		margin-right: 1.4rem;
		margin-bottom: 0;
	}
}

.blog_section__decor_dot {
	&:nth-child(1) {
		background-color: #fb3b1e;
	}

	&:nth-child(2) {
		background-color: var(--green_color);
	}

	&:nth-child(3) {
		background-color: var(--primary_light_color);
	}

	@include bp($point_2, min) {
		width: 1.2rem;
		height: 1.2rem;

		&:not(:last-child) {
			margin-right: 1.5rem;
		}
	}

	@include bp($point_3, $point_4) {
		width: 1rem;
		height: 1rem;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 0.8rem;
		height: 0.8rem;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}

.blog_section__subtitle {
	@extend %base_text_2;

	@include bp($point_2, min) {
		margin-right: 18rem;
		margin-bottom: 0.4rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
		margin-bottom: 0.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.33rem;
		margin-bottom: 0;
	}
}

.hero_article {
	position: relative;

	@include flex_block(row, wrap, space-between, stretch);

	background-color: var(--primary_dark_color);

	@include bp($point_3, $point_4) {
		height: 100vw;
		// max-height: 76.8rem;
		margin: 0 calc(calc(var(--gl-indent) * -1));
	}
	@include bp($point_4 - 1) {
		height: 100vw;
		// max-height: 32rem;
		margin: 0 calc(calc(var(--gl-indent) * -1));
	}
}

.hero_article__image {
	position: relative;

	overflow: hidden;

	@include bp($point_2, min) {
		width: 50%;
		padding-bottom: 50%;
	}

	@include bp($point_3) {
		width: 100%;
		height: 100%;
	}
}

.hero_article_bg__img {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;

	transition: transform 1.5s ease;
	will-change: transform;

	.hero_article__image:hover & {
		transform: scale(1.1);
	}
}

.hero_article__content {
	@extend %global_flex_block_row_wrap_flex-start_center;

	width: 50%;

	@include bp($point_2, min) {
		padding: 21rem 12rem 9rem;
	}

	@include bp($point_3) {
		position: absolute;
		left: 0;
		top: 0;

		width: 100%;
		height: 100%;

		background-color: rgba(14, 30, 69, 0.72);
	}

	@include bp($point_3, $point_4) {
		padding: 23.5rem 10.6rem 10rem;
	}

	@include bp($point_4 - 1) {
		padding: 8.9rem 4rem 3.6rem;
	}
}

.hero_article__content_inner {
	@extend %global_flex_block_column_nowrap_flex-end;

	height: 100%;
}

.hero_article__meta {
	@extend %global_flex_block_row_wrap_flex-start_center;

	@include bp($point_2, min) {
		margin-bottom: 5.6rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 4.2rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1.6rem;
	}
}

.hero_article__meta_item {
	@extend %tag_1;
	@extend %global_flex_block_row_wrap_flex-start_center;

	&:not(:last-child) {
		&:after {
			content: "";
			display: block;
			width: 0.4rem;
			height: 0.4rem;

			@include bp($point_2, min) {
				margin: 0 2.4rem 0 1.9rem;
				background-color: var(--gray_color);
			}

			@include bp($point_3) {
				margin: 0 1.4rem;
				background-color: var(--white_color);
			}
		}
	}

	@include bp($point_4 - 1) {
		display: none;

		font-size: 1.3rem;
	}
}

.hero_article__tag {
	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: #fff;
		text-decoration: none;
	}
	@include bp($point_4 - 1) {
		display: block;

		span {
			display: none;
		}

		&:not(:last-child):after {
			display: none;
		}
	}
}

.hero_article__title {
	font-weight: 600;
	color: #fff;
	letter-spacing: -0.03em;

	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: var(--primary_light_color);
		text-decoration: none;
	}

	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	@include bp($point_2, min) {
		font-size: 4.8rem;
		line-height: 1.25em;
	}

	@include bp($point_3, $point_4) {
		font-size: 4rem;
		line-height: 1.1em;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		line-height: 1.26em;
	}
}

.hero_article__author {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin: auto auto 0 0;
}

.hero_article__author_avatar {
	display: block;

	object-fit: contain;

	@include bp($point_2, min) {
		width: 4.8rem;
		height: 4.8rem;
		margin: 0 2rem 0 0;
	}

	@include bp($point_3, $point_4) {
		width: 7.2rem;
		height: 7.2rem;
		margin: 0 2.4rem 0 0;
	}

	@include bp($point_4 - 1) {
		width: 4rem;
		height: 4rem;
		margin: 0 1.6rem 0 0;
	}
}

.hero_article__author_name {
	color: var(--white_color);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.4em;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
		line-height: 2.56em;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
		line-height: 1.3em;
	}
}

.hero_article__icon {
	position: absolute;
	right: 0;
	top: 0;

	@include bp($point_2, min) {
		width: 13rem;
		height: 13rem;
	}

	@include bp($point_3, $point_4) {
		width: 16rem;
		height: 16rem;
	}

	@include bp($point_4 - 1) {
		width: 7.2rem;
		height: 7.2rem;
	}
}

.hero_article__icon img {
	@extend %contain_img;
}
